// Breakpoints
$xs: 576;
$sm: 768;
$md: 992;
$lg: 1200;

// colors
$blue: #405089;
$orange: #ff7555;

* {
	box-sizing: border-box; }

html {
    font-size: 16px; }
* {
	font-family: 'GothamPro';
	font-weight: 300; }
body {
	font-size: 1rem;
	line-height: 1.4 {
    background: #ccc; } }

.container {
    width: 100%;
    max-width: 540px;
    margin: 0 auto;
    padding: 0 20px;
    @include rmin($sm) {
        max-width: 720px; }
    @include rmin($md) {
        max-width: 960px; }
    @include rmin($lg) {
        max-width: 1170px; } }

// General
img {
	max-width: 100%;
	font-size: 0;
	line-height: 0;
	display: block; }

a {
	text-decoration: none;
	&:hover {
		text-decoration: none; } }

p {
	margin-bottom: 1rem; }

h1 {
	font-weight: 400; }

.mb-1 {
	margin-bottom: 1rem; }
.mb-2 {
	margin-bottom: 2rem; }
.mb-3 {
	margin-bottom: 3rem; }

// other style
.logo {
	padding: 1rem 0.625rem;
	background: $orange;
	color: #ffffff;
	font-weight: 700;
	display: inline-block;
	border-radius: 10px;
	font-size: 1.25rem; }



// button's
.btn {
	min-width: 9rem;
	display: inline-block;
	padding: 0.625rem;
	border: 1px solid;
	border-radius: 25px;
	text-align: center;
	font-weight: 400;
	line-height: 1.2rem;
	@include tr(all ease 0.2s);
	&--blue {
		background: $blue;
		color: #ffffff;
		border-color: #ffffff; }
	&--white {
		background: #ffffff;
		color: $blue;
		border-color: #ffffff; }
	&--gh {
		color: #ffffff;
		@include gh(#5EB6FE,#62d1fd); }
	&:hover {
		opacity: 0.75; }
	&--ml {
		margin-left: 1rem; }

	// ctour background btn
	&--ctour {
		background-image: url(../img/ctour/btn.jpg);
		background-repeat: no-repeat;
		background-position: center right;
		background-size: contain;
		border-color: transparent;
		box-shadow: 0 0 10px 3px rgba(0,0,0,.04);
		padding: 1.5rem 8rem 1.5rem 1.5rem;
		border-radius: 35px;
		font-size: 1.125rem;
		font-weight: 500;
		color: $blue;
		display: inline-block; } }


// btn and submit
.btn-submit {
	background-image: -webkit-gradient(linear,left top,right top,from(#5eb6fe),to(#62d1fd));
	background-image: -webkit-linear-gradient(left,#5eb6fe,#62d1fd);
	background-image: -o-linear-gradient(left,#5eb6fe,#62d1fd);
	background-image: linear-gradient(left,#5eb6fe,#62d1fd);
	font-size: 1rem;
	text-indent: 0;
	padding: .625rem 2rem;
	border-radius: 25px;
	color: #fff;
	&--success {
		background-image: none;
		background-color: $blue; }
	&--error {
		background-image: none;
		background-color: $orange; } }

.btn-more {
	border-radius: 25px;
	padding: 0.685rem 3rem 0.685rem 1rem;
	background-color: #405089;
	color: #ffffff;
	cursor: pointer;
	position: relative;
	display: inline-block;
	&:after {
		content: ' ';
		position: absolute;
		top: 0.25rem;
		right: 0.25rem;
		width: 2.125rem;
		height: 2.125rem;
		background-color: #fff;
		border-radius: 25px;
		@include tr(all ease 0.3s); }
	&:before {
		background-image: url(data:image/svg+xml;utf8;base64,);
		background-size: contain;
		background-position: center center;
		content: ' ';
		width: 1rem;
		height: 1rem;
		position: absolute;
		top: 0.825rem;
		right: 0.825rem;
		z-index: 2;
		transform: rotate(90deg);
		@include tr(all ease 0.3s); }
	&:hover:after {
		transform: scale(1.1); } }


// flex helper
.flex-center {
	display: flex;
	flex-wrap: wrap;
	justify-content: center; }
.flex-right {
	display: flex;
	flex-wrap: wrap;
	justify-content: flex-end; }





// timeline
.timeline {
	&__wrap {
		position: relative;
		overflow: hidden;
		width: 100%;
		height: 100%; }
	&__line {
		height: 4px;
		background: $blue;
		width: 100%;
		border-radius: 5px;
		position: absolute;
		bottom: 3px; }
	&__list {
		width: 100%;
		display: flex;
		flex-wrap: nowrap;
		overflow-x: auto;
		-webkit-overflow-scrolling: touch;
		&::-webkit-scrollbar {
    		display: none; } }
	&__item {
		color: #666666;
		font-size: 0.875rem;
		font-weight: 400;
		text-align: center;
		display: flex;
		flex-direction: column;
		justify-content: flex-end;
		align-content: center;
		align-items: center;
		max-width: 4rem;
		line-height: 1.1;
		margin-left: 15%;
		position: relative;
		z-index: 2;
		&:first-child {
			margin-left: 0; } }
	&__dot {
		background: #ffffff;
		border-radius: 50%;
		border: 2px solid $blue;
		width: 10px;
		height: 10px;
		margin-top: 0.25rem; } }




// owl carousel
.owl-carousel {
	& .owl-dots {
		display: flex;
		justify-content: center;
		justify-items: center;
		margin-top: 1.75rem; }
	& .owl-dot {
			width: 0.625rem;
			height: 0.625rem;
			background: #585f76 !important;
			border-radius: 50%;
			margin: 0 0.25rem;
			opacity: 0.25;
			@include tr(all ease 0.3s);
			&:hover {
				opacity: 1; }
			&.active {
				opacity: 1; } } }


// form
.form {
	&__group {
		margin-bottom: 1.5rem;
		&--plgrey input::placeholder, &--plgrey select {
			color: #bababa !important; } }
	& input[type=text], input[type=password] {
		background: transparent;
		font-size: 0.750rem;
		color: #405089;
		border-bottom: 1px solid #e7e7e8;
		width: 100%;
		padding: 0.5rem 0;
		font-weight: 500;
		&::placeholder {
			color: #405089;
			font-weight: 500; } }

	& textarea {
		width: 100%;
		border: 1px solid #eeeeee;
		border-radius: 25px;
		font-size: 0.75rem;
		padding: 1rem;
		font-weight: 500;
		&::placeholder {
			color: #405089;
			font-weight: 500; } }
	&__forgot-password {
		color: #bababa;
		font-size: 0.75rem;
		display: flex;
		justify-content: flex-end;
		margin-top: -1.25rem;
		margin-bottom: 1.5rem;
		text-decoration: dashed; }

	&__label {
		color: #bababa;
		font-size: 0.75rem;
		font-weight: 500;
		&--blue {
			color: $blue; } }
	&__select {
		-webkit-appearance: none;
		-moz-appearance: none;
		appearance: none;
		border-radius: 0;
		background: transparent;
		font-size: 0.825rem;
		color: #405089;
		border: 0;
		border-bottom: 1px solid #e7e7e8;
		width: 100%;
		padding: 0.5rem 0;
		font-weight: 500; }


	// custom-checkbox
	&__custom-checkbox {
		& label {
			padding-left: 1.125rem;
			overflow: hidden;
			position: relative;
			&:after {
				content: ' ';
				position: absolute;
				left: 0;
				bottom: 0;
				width: 0.5rem;
				height: 0.5rem;
				padding: 0.063rem;
				border-radius: 3px;
				background: transparent;
				border: 1px solid #cccccc;
				box-sizing: content-box; } }
		& input[type="checkbox"] {
			position: absolute;
			left: -99999rem; }
		& input[type="checkbox"]:checked + label:after {
			background-image: url(../img/op/checkbox.svg);
			background-repeat: no-repeat;
			background-position: center center;
			background-size: 85% 85%;
			border: 1px solid $blue; } }

	// radio
	&__radio-group {
		display: flex; }
	&__radio {
		margin-right: 1rem;
		& input[type="radio"] {
			position: absolute;
			left: -9999999px; }
		& label {
			padding-left: 1.125rem;
			position: relative;
			&:after {
				content: ' ';
				position: absolute;
				left: 0;
				bottom: 0;
				width: 0.75rem;
				height: 0.75rem;
				border: 4px solid $blue;
				background: $blue;
				border-radius: 50px; } }
		& input[type="radio"]:checked + label:after {
			background: #5fbafe; } }
	&__helper {
		font-size: 0.75rem;
		color: #bababa; }
	&__group &__helper {
		margin-top: 0.375rem; } }




// footer
.footer {
	background: #f7f8f8;
	padding-top: 2rem;
	padding-bottom: 2rem;
	&__wrap {
		display: flex;
		flex-wrap: wrap;
		align-items: center; }
	&__copyright {
		margin-top: 1rem;
		@include rmin($sm) {
			margin-top: 0;
			margin-left: 2rem; }
		& p, & a {
			color: #404040;
			margin-bottom: 0.125rem;
			font-size: 0.875rem; }
		& a {
			border-bottom: 1px solid #404040;
			@include tr(border ease 0.3s);
			&:hover {
				border-color: $orange; } } }
	&__social {
		display: flex;
		margin-left: auto;
		width: 100%;
		margin-top: 1.5rem;
		margin-left: -0.5rem;
		@include rmin($sm) {
			width: auto;
			margin-top: 0;
			margin-left: auto; }
		&-link {
			display: block;
			margin-left: 0.5rem; }
		&-icon {
			border-radius: 50%;
			border: 1px solid #000000;
			width: 1.5rem;
			height: 1.5rem;
			padding: 0.325rem;
			fill: #000000;
			@include tr(all ease 0.3s);
			&:hover {
				background: $orange;
				fill: #ffffff;
				border-color: $orange;
				transform: scale(1.3); } } } }
